
* {
  margin: 0;
  padding: 0;
}
html {
  overflow-x: hidden;
  background: #EEEEEE;
}
body {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  overflow-x: hidden;
}
#app {
  font-family: sans-serif;
  width: 100%;
  height: 100%;
}
.content-warper {
    margin-top: 60px;
}
.swiper-box {
    width: 100%;
    height: 100%;
    margin: 0 auto;
}
.swiper-item {
    height: 100%;
}
.swiper-pagination-bullet-custom {
    width: 100% !important;
    height: 100% !important;
    margin: 0 !important;
    text-align: center;
    line-height: 50px;
    color: #999999;
    border-radius: 0 !important;
    background: #fff !important;
    opacity: 1 !important;
}
.swiper-pagination-bullet-custom.swiper-pagination-bullet-active {
    color: #000;
}
.swiper-pagination {
    top: 0;
    height: 50px;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-orient: horizontal;
    -webkit-box-direction: normal;
        -ms-flex-direction: row;
            flex-direction: row;
    -ms-flex-pack: distribute;
        justify-content: space-around;
    width: 100%;
}




